*, :before, :after {
  box-sizing: border-box;
}

html {
  text-size-adjust: none;
  vertical-align: baseline;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin: 0;
  margin-block-end: 0;
  padding: 0;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

body {
  min-height: 100vh;
  line-height: 1.5;
}

h1, h2, h3, h4 {
  text-wrap: balance;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:target {
  scroll-margin-block: 5ex;
}

